import { ProductsService } from './../../services/products.service';
import { Component, OnInit, AfterViewChecked,} from '@angular/core';

declare let paypal:any;

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})


export class CheckoutComponent implements OnInit,AfterViewChecked {


  pScript:boolean=false;

  getItems=JSON.parse(localStorage.getItem('cart'))
  priceArray;
  resData;

  singlePrice;
  total;


  constructor(
    private proSer:ProductsService
  ) { }

  ngOnInit(): void {
    
    // match By Id
    this.getItems.forEach(v => {
      this.proSer.getPriceOfProduct(v._id).subscribe(
        res => {
          this.resData=res;
          if(this.resData.err==0){
            this.priceArray=this.priceArray || [];
            this.priceArray.push(this.resData.msg.price)
          }
        }
      )

      setTimeout(()=>{
        console.log(this.priceArray);
        
        for(let i=0;i<this.getItems.length;i++){
          if(this.getItems[i].price==this.priceArray[i]){
            this.priceArray.reduce((p,c)=>{

              //0+1100*1 = 1100 =p

              // 1100+50*6 => 1400

              this.total= parseFloat(p) + (parseFloat(c)*parseFloat(this.getItems[i].quantity));
            })
          }
        }
      },2000)

      setTimeout(() => {
        console.log(this.total);
        
      }, 3000);
      
      
    });
    

  }



  payNow(){
    let a=10;
    this.proSer.paypal(this.getItems).subscribe(
      res =>{
        console.log(res);
        
      }
    )
  }

  ngAfterViewChecked():void{
    if(!this.pScript){
      this.addPaypalScript().then(()=>{
        paypal.Button.render(this.paypalConig,'#paypal-btn')
      })
    }
  }


  addPaypalScript(){
    this.pScript=true;
    return new Promise((res,rej)=>{
      let scriptEl=document.createElement('script');
      scriptEl.src="https://www.paypalobjects.com/api/checkout.js";
      scriptEl.onload=res;
      document.body.appendChild(scriptEl)
    })
  }


  // paypaltest
  paypalConig={
    env: 'sandbox',
      client: {
        sandbox: 'AcV17fkejFqAQmSR5VXupit95_Uk7dS5VANkes2iCN-eV75s4SJ8XyydmG3KXkpfUDrmC69rJEqj6aua',
        // production: 'demo_production_client_id'
      },
      commit: true,
       // Set up a payment
       payment: function(data, actions) {
        return actions.payment.create({
          transactions: [{
            amount: {
              total: "10",
              currency: 'INR'
            }
          }]
        });
      },
      onAuthorize: function(data, actions) {
        return actions.payment.execute().then(function() {
          console.log('success');
          
        });
      }
  }

}
