import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ProductsService {

  constructor(
    private http:HttpClient
  ) { }


  getProductsByCat(catName){
    return this.http.get(`${environment.api_url}front/products/${catName}`)
  }

  getAllProducts(){
    return this.http.get(`${environment.api_url}products`)
  }

  getSingleProduct(pid){
    return this.http.get(`${environment.api_url}front/product/${pid}`)
  }

  getPriceOfProduct(pid){
    return this.http.get(`${environment.api_url}front/getprice/${pid}`)
  }


  paypal(data){
    return this.http.post(`${environment.api_url}front/payment`,data)
  }

}
