import { ProductsService } from '../../services/products.service';

import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CategoryService } from 'src/app/services/category.service';

@Component({
  selector: 'app-cat',
  templateUrl: './cat.component.html',
  styleUrls: ['./cat.component.css']
})
export class CatComponent implements OnInit {

  catId;
  resData;
  catData;
  catName;
  constructor(
    private ar:ActivatedRoute,
    private catSer:CategoryService,
    private productSer:ProductsService
  ) {

    // get cat id
      let id=this.ar.params.subscribe(res=>{
        this.catId=res;
        this.catId=this.catId.id;       
      })

      // get cat name
      setInterval(()=>{
        this.catSer.getSingleCategory(this.catId).subscribe(
          res=>{
            this.resData=res;
            this.catData=this.resData.msg;
            this.catName=this.catData.cname;
          }
        )
      },1000)

     


   }

  ngOnInit(): void {
     // get products by cat

     setInterval(()=>{
      this.productSer.getProductsByCat(this.catName).subscribe(
        res=>{
          console.log(res);
          
        }
      )
     },2000)
  }

}
